.app-wrapper {
    background: rgba(50, 50, 50, .05);
    font-family: 'Montserrat', sans-serif;
    width: content-box;
    padding: 5px;
    min-height: 100vh;
    display: grid;
    grid-template-areas: "header header" "nav content";
    grid-template-rows: 80px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-gap: 5px;
}

.app-wrapper-content {
    grid-area: content;

}

a {
    text-decoration: none;
    color: rgba(75, 80, 240, .9);
}

a.active {
    color: rgba(221, 20, 61, .7);
    text-decoration: none;
}

a:hover{
    font-weight: bold;
    outline: none;
    transition: all .15s linear;
}

button {
    outline: none;
}







